    color: white;
    font-size: 20px;
}
input{
    margin: 0px 0px 10px 0px;
    height: 25px;
    border-radius: 5px;
    border:grey 1px solid

}
textarea{
    margin-bottom: 10px;
    border-radius: 5px;
}
button{
    margin: 10px 0px 10px 0px;
    background-color:rgb(236, 105, 105) ;
    border: none;
    color: white;
    font-size: 18px;
    border-radius: 5px;
    font-family: sans-serif;
}

